.list {
  width: 160px;
  padding: 10px;
  margin-right: 10px;
  box-shadow: 2px 5px #888888;
}
.title {
  font-size: 23px;
}
.cardWrapper {
  margin-bottom: 6px;
}

.head {
  display: flex;
  justify-content: space-between;
  margin-bottom: 20px;
}

.cardWrapper {
  border: none;
  border-radius: 6px;
}

.card {
  margin: 10px 0px;
  margin: 3px 0px;
  background: #888888;
  padding: 5px 10px;
  border-radius: 6px;
  color: white;
}
